@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');
 html {
	 box-sizing: border-box;
}
 *, *:before, *:after {
	 box-sizing: inherit;
}
 body {
	 margin: 0;
	 padding: 0;
	 line-height: 1;
	 font-family: "Roboto", sans-serif;
	 color: #202020;
	 background-color: #fafafa;
}
 .header {
	 border-bottom: solid 1px #ca2100;
	 background-color: #db4c3f;
	 box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
	 height: 44px;
	 width: 100%;
}
 .header img {
	 width: 24px;
}
 .header nav {
	 height: 100%;
	 max-width: 922px;
	 display: flex;
	 align-items: center;
	 margin: auto;
}
 .content {
	 display: flex;
	 max-width: 922px;
	 margin: auto;
}
 .sidebar {
	 user-select: none;
	 width: 266px;
	 height: 100vh;
	 padding-top: 74px;
	 border-right: 1px solid #f1f1f1;
	 background-color: #fafafa;
}
 .sidebar div {
	 margin-bottom: 10px;
	 cursor: pointer;
	 padding: 15px;
}
 .sidebar div.active {
	 background-color: white;
}
 .sidebar div .icon {
	 margin-right: 10px;
}
 .tasks {
	 background-color: white;
	 height: 100vh;
	 width: calc(922px - 266px);
	 padding: 74px 20px 0;
}
 .tasks .add-task-btn {
	 cursor: pointer;
}
 .tasks .add-task-btn .plus {
	 color: #dd4b39;
	 font-size: 18px;
}
 .tasks .add-task-btn .add-task-text {
	 color: #545454;
	 font-size: 14px;
	 margin-left: 15px;
}
 .tasks .add-task-dialog {
	 margin-top: 10px;
}
 .tasks .add-task-dialog input {
	 width: 100%;
	 border: 1px solid #ddd;
	 background-color: white;
	 border-radius: 3px;
	 height: 35px;
	 padding-left: 10px;
	 padding-right: 10px;
}
 .tasks .add-task-dialog .add-task-actions-container {
	 margin-top: 10px;
	 display: flex;
	 justify-content: space-between;
}
 .tasks .add-task-dialog .add-task-actions-container .add-btn, .tasks .add-task-dialog .add-task-actions-container .cancel-btn {
	 width: 120px;
	 margin: 2px;
	 padding: 5px;
	 border-radius: 5px;
	 font-weight: 800;
	 cursor: pointer;
	 border: none;
}
 .tasks .add-task-dialog .add-task-actions-container .add-btn {
	 background-color: #db4c3f;
	 color: white;
}
 .tasks .add-task-dialog .add-task-actions-container .add-btn:disabled {
	 background: #ccc;
	 color: #202020;
}
 .tasks .add-task-dialog .add-task-actions-container .cancel-btn {
	 background-color: inherit;
	 border: none;
	 color: grey;
}
 .tasks .task-items-container {
	 margin-top: 40px;
	 padding: 0 20px;
}
 .tasks .task-items-container .task-item {
	 display: flex;
	 justify-content: space-between;
}
 .tasks .task-items-container .task-item:not(:last-child) {
	 border-bottom: 1px solid #db4c3f;
}
 